.chat {
    flex: 2;
    .chatInfo {
        height: 70px;
        background-color: #5d5b8d;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        color: lightgray;
    }
    .chatIcons {
        display: flex;
        gap: 10px;
        img {
            height: 24px;
            cursor: pointer;
        }
    }
}