@import '../mixin';

.navbar {
    display: flex;
    align-items: center;
    background-color: #2f2d52;
    height: 50px;
    padding: 10px;
    justify-content: space-between;
    color: #ddddf7;  //sass變數
    .logo {
        font-weight: bold;
        @include tablet {
            display: none;
        }
    }
    .user {
        display: flex;
        align-items: center;
        gap: 10px;
        img {
            background-color: #ddddf7;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            object-fit: cover;
        }
        button {
            background-color: #5d5b8d;
            color: #ddddf7;
            font-size: 10px;
            padding: 10px;
            border: none;
            cursor: pointer;
            @include tablet {
                position: absolute;
                bottom: 10px;
            }
        }
    }
}
